.footer {
  background-color: #222;
  color: #fff;
  padding: 20px 10px;
  text-align: center;
}

.footer-content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}

.footer-menu {
  text-align: left;
}

.footer-menu h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.footer-menu ul {
  list-style: none;
  padding: 0;
}

.footer-menu ul li {
  margin: 5px 0;
}

.footer-menu ul li a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-menu ul li a:hover {
  color: #00bcd4;
}

.footer-copy {
  margin-top: 20px;
  font-size: 0.9rem;
}
